<template>

  <v-card>
    <v-card-title>
    </v-card-title>
    <v-card-text>
      <v-row>
        <emailinput :selectlist='selectlist'
                    :function_uuid='function_uuid'
                    :updateFunction_uuid='updateFunction_uuid'
                    :showlist='showlist'
                    :propertylist='propertylist'
                    @getpropertylist='getpropertylist'
                    :readonly='disabled'>
        </emailinput>
        <emailinput :selectlist='selectlist'
                    :function_uuid='function_uuid'
                    :updateFunction_uuid='updateFunction_uuid'
                    :readonly='disabled'
                    inputid='deputy'
                    @getpropertylist='deputygetpropertylist'
                    :showlist='deputyshowlist'
                    :propertylist='deputypropertylist'
                    :headertext="$t('label_Deputy_to_the_addressee')">
        </emailinput>

        <v-col cols="12"
               style="position:relative">
          <inputtemplate v-model="emailsubject"
                         :readonly='disabled'
                         :function_uuid='function_uuid'
                         :updateFunction_uuid='updateFunction_uuid'
                         :defaultemailsubject='defaultemailsubject'></inputtemplate>
        </v-col>
        <v-col cols="12">
          <textareatemplate :defaultcontent='defaultemailcontent'
                            :readonly='disabled'
                            :function_uuid='function_uuid'
                            :updateFunction_uuid='updateFunction_uuid'
                            v-model="emailcontent"></textareatemplate>
        </v-col>
      </v-row>
    </v-card-text>

    <template v-if="$store.state.uuid">
      <mrc_ess_submitBtn @cancel='cancel'
                         @success='success'
                         v-if="!disabled"></mrc_ess_submitBtn>
    </template>
    <template v-else>
      <mrc_ess_submitBtn @cancel='addcancel'
                         @success='addsuccess'
                         :cancelText="$t('label_The_previous_step')"
                         v-if="!disabled"></mrc_ess_submitBtn>
    </template>

  </v-card>

</template>

<script>
import { getemailtemplatelist, getdetailemailtemplate, updateemailtemplate } from '@/service/api/emailtemplate'
import { getBusinessList } from '@/service/api/prompt.js'
import emailinput from './emailinput'
import propertyDialog from './propertyDialog'
import textareatemplate from './textareatemplate'
import inputtemplate from './inputtemplate'
// import mrc_ess_combox from './mrc_ess_combox'
export default {
  components: {
    emailinput, propertyDialog, textareatemplate, inputtemplate
  },
  props: {
    disabled: {
      type: Boolean
    },
    function_uuid: {
      type: String
    }
  },
  data () {
    return {
      selectlist: [],
      showlist: [],
      propertylist: [],
      deputyshowlist: [],
      deputypropertylist: [],
      emailsubject: [],
      emailcontent: [],
      inputstatus: false,//
      tab: null,
      items: [
        { tab: '功能变量' },
        { tab: '系统变量' },
      ],
      list: {},
      dialog: false,
      recipientsitems: [],
      recipients: [],
      childrenpropertylist: [],
      updateFunction_uuid: ''
    };
  },
  computed: {
    defaultemailsubject () {
      const lang = this.$store.state.lang  //当前选择的语言
      const text = this.emailsubject.filter(item => {
        return item.language_code == lang
      })
      return text.length > 0 ? text[0].content : '请设置内容'
    },
    defaultemailcontent () {
      const lang = this.$store.state.lang  //当前选择的语言
      const text = this.emailcontent.filter(item => {
        return item.language_code == lang
      })
      return text.length > 0 ? text[0].content : '请设置内容'
    }
  },
  created () { },
  watch: {
    TSemailsubject: {
      handler (v) {
        console.log(v, '只改变了');
      }
    }
  },
  async mounted () {
    await this.getselectlist()
    if (this.$store.state.uuid) {
      await this.getdetail()
    }
    else {
      this.$store.state.currentCompanyLanguage.map(item => {
        this.emailsubject.push({
          "language_code": item.value,    //国际化代码
          "content_type": "1",    //国际化字段(1:邮件主题  2:邮件内容)
          "content": ''
        })
        this.emailcontent.push({
          "language_code": item.value,    //国际化代码
          "content_type": "2",    //国际化字段(1:邮件主题  2:邮件内容)
          "content": ''
        })
      })
      // this.emailsubject = [
      //   {
      //     "language_code": "zh",    //国际化代码
      //     "content_type": "1",    //国际化字段(1:邮件主题  2:邮件内容)
      //     "content": ''
      //   },
      //   {
      //     "language_code": "cn",
      //     "content_type": "1",
      //     "content": ''
      //   }
      // ]
      // this.emailcontent = [
      //   {
      //     "language_code": "zh",
      //     "content_type": "2",
      //     "content": ''
      //   },
      //   {
      //     "language_code": "cn",
      //     "content_type": "2",
      //     "content": ''
      //   }
      // ]
    }
  },

  methods: {
    async getdetail () {
      const res = await getdetailemailtemplate({ uuid: this.$store.state.uuid })
      if (res.code == 200) {
        // this.list = res.data
        // const propertylist = []
        // res.data.template_carbon_copy = res.data.template_carbon_copy + ',{{ijjjj}},{{sasxx}}'
        // res.data.template_addressee = res.data.template_addressee + ',{{saaas}},{{qqqws}}'
        // 主收件人
        const propertylist = []
        const showlist = []
        const deputypropertylist = []
        let deputyshowlist = []
        const emailsubject = []
        const emailcontent = []
        this.updateFunction_uuid = res.data.function_uuid
        if (res.data.template_addressee.indexOf(',') != -1) {
          res.data.template_addressee.split(',').map(item => {
            if (item.indexOf('{{') == 0) {
              propertylist.push({ 'property': item, 'divbgd': true })
            }
            else {
              showlist.push(item)
            }
          })
        }
        else {
          if (res.data.template_addressee.indexOf('{{') == 0) {
            propertylist.push({ 'property': res.data.template_addressee, 'divbgd': true })
          }
          else {
            showlist.push(res.data.template_addressee)
          }
        }
        // 副收件人
        console.log(res.data.template_carbon_copy);
        if (res.data.template_carbon_copy.indexOf(',') != -1) {
          res.data.template_carbon_copy.split(',').map(item => {
            if (item.indexOf('{{') == 0) {
              deputypropertylist.push({ 'property': item, 'divbgd': true })
            }
            else {
              deputyshowlist.push(item)
            }
          })
        }
        else {
          if (res.data.template_carbon_copy.indexOf('{{') != -1) {
            deputypropertylist.push({ 'property': res.data.template_carbon_copy, 'divbgd': true })
          }
          else {
            deputyshowlist.push(res.data.template_carbon_copy)
          }
        }
        // 主旨和内容
        res.mrbase_sys_mail_template_language.map(item => {
          if (item.content_type == 1) {
            item.content = item.content.replace(/<br\/>/g, "\n")
            emailsubject.push(item)
          }
          if (item.content_type == 2) {
            item.content = item.content.replace(/<br\/>/g, "\n")
            emailcontent.push(item)
          }
        })
        this.propertylist = propertylist
        this.showlist = showlist
        this.deputypropertylist = deputypropertylist
        this.deputyshowlist = deputyshowlist[0] != '' ? deputyshowlist : []
        this.emailsubject = emailsubject
        this.emailcontent = emailcontent
      }
    },
    async getselectlist () {
      const currentCompany = this.$store.state.currentCompany
      const res = await getBusinessList({
        prompt_name: 'recipients_staff', uuid: currentCompany
      })
      if (res.code == 200) {
        this.selectlist = res.data
      }
    },
    cancel () {
      this.$myConfirm({
        content: '确认取消编辑'
      }).then(res => {
        this.$router.push({
          name: 'emailtemplate'
        })
      }).catch(err => { })
    },
    datatreating () {
      let showlist = this.showlist.join(',')
      // console.log(this.propertylist);
      this.propertylist.map(item => {
        showlist += item.property
      })
      // console.log(showlist);
      let deputyshowlist = this.deputyshowlist.join(',')
      this.deputypropertylist.map(item => {
        deputyshowlist += item.property
      })

      let emailsubject = this.$clone(this.$deletefield(this.emailsubject)).map(item => {
        item.content.replace(/\n/g, "<br/>")
        return item
      })
      let emailcontent = this.$clone(this.$deletefield(this.emailcontent)).map(item => {
        item.content = item.content.replace(/\n/g, "<br/>")
        return item
      })

      // console.log(emailsubject, emailcontent)
      // return
      let mrbase_sys_mail_template_language = [...emailsubject, ...emailcontent]
      mrbase_sys_mail_template_language = mrbase_sys_mail_template_language.map(item => {
        delete item.id
        delete item.template_uuid
        return item
      })
      // console.log(this.deputyshowlist);
      // console.log(this.deputypropertylist);
      // console.log(this.emailsubject);
      // console.log(this.emailcontent);
      console.log(mrbase_sys_mail_template_language);
      return {
        mrbase_sys_mail_template_language,
        template_addressee: showlist,
        template_carbon_copy: deputyshowlist,
      }
    },
    success () {
      updateemailtemplate({
        uuid: this.$store.state.uuid, ...this.datatreating()
      }).then(res => {
        if (res.code == 200) {
          this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
          this.getdetail()
        }
        else {
          this.$store.commit('newsTips', res.msg)
        }
      })
    },
    addcancel () {
      this.$emit('changee1', 1)
    },
    addsuccess () {
      this.$emit('submit', this.datatreating())
    },
    getpropertylist (v) {
      this.propertylist = v
    },
    deputygetpropertylist (v) {
      this.deputypropertylist = v
    },
  },
};
</script>

<style scoped>
.box /deep/ .v-text-field__details {
  min-height: 0px;
}
.box /deep/ .v-messages {
  min-height: 0px;
  height: 0px;
}
</style>
